body {
    margin: 0px;
    padding: 0px;
}
* {
    box-sizing: border-box;
}
#root {
    height: 100vh;
    width: 100vw;
    background-color: teal;
    display: flex;
    flex-direction: row;
}
.board{
    height: 100%;
    width: 50%;
    border: 2px solid white;
    display: flex;
    flex-direction: column;
}
.display{
    height: 100%;
    width: 50%;
    border: 2px solid white;
    display: flex;
    flex-direction: column;
}
.title{
    height: 10%;
    width: 100%;
    border: 2px solid black;
    text-align: center;
    font-size: 50px;
    color: white;
    /* margin: 0px 10px; */
}
.content{
    height: 90%;
    width: 100%;
    /* border: 2px solid red; */
}
.ttt{
    height: 75%;
    width: 75%;
    border: 3px solid white;
    margin: 75px auto;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
}
.row{
    height: 100%;
    border-bottom: 1px solid white;
    display: flex;
    flex-direction: row;
}
.row button{
    width: 100%;
    cursor: pointer;
    font-size: 100px;
}
.row button:hover{
    background-color: antiquewhite;
}
.history{
    height: 75%;
    width: 75%;
    border: 3px solid white;
    margin: 75px auto;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    
}
.history button{
  
    padding: 20px;
    font-size: 20px;
    cursor: pointer;
    
}
.history button:hover{
    background-color: aqua;
}
